import { Component, OnInit } from '@angular/core';
import { NgForm } from '@angular/forms';
import { Vendor } from '../vendor/vendor';
import { Router, ActivatedRoute } from '@angular/router';
import { AddvendorService } from './addvendor.service';

@Component({
  selector: 'app-addvendor',
  templateUrl: './addvendor.component.html',
  styleUrls: ['./addvendor.component.scss']
})
export class AddvendorComponent implements OnInit {

  vendors:Vendor[];
  vendor=new Vendor();
  vendorId: any;

  constructor(private router: Router,private service: AddvendorService, private route:ActivatedRoute) { }

  ngOnInit() {
    this.route.params.subscribe( params => this.getById(params['vendorId']));
  }
  addVendor():void{
    this.service.insertVendor(this.vendor)
    .subscribe((response) => {
      console.log(response);
      
       this.getVendor;
      this.router.navigate(['vendor'])
      this.resetForm();
    },(error) => {
      console.log(error);
    });
  }
  resetForm(form?:NgForm){
    if(form!=null)
    form.resetForm();
    this.service.formData={
      
      vendorId: '',
      vendorName:'',
      address: '',
      location: '',
      service: '',
      pincode: '',
      isActive: '',
      contactId: '',
      contactName:'',
      department: '',
      email: '',
      phone : ''           
    }                                  
  }
  getVendor():void{
    this.service.getAllVendors()
    .subscribe((data)=>{
      this.vendors=data;
      console.log(data);

    },(error)=>{
      console.log(error);

    });
  }
getById(vendorId:any){
  this.service.getVendorById(vendorId)
  .subscribe((data)=>{
    this.vendor=data;
    console.log(data);
  },(error) =>{
    console.log(error);
  
  });
}

  
  logout(){
    this.service.logout();

  }


}