import { Component, OnInit } from '@angular/core';
import { Vendor } from '../vendor';
import { VendorService } from '../vendor.service';
import { ActivatedRoute } from '@angular/router';
import { AuthService } from 'src/app/service/auth.service';

@Component({
  selector: 'app-view-contact',
  templateUrl: './view-contact.component.html',
  styleUrls: ['./view-contact.component.scss']
})
export class ViewContactComponent implements OnInit {
  constructor(private authService:AuthService,private _service:VendorService,private route:ActivatedRoute) { }

  ngOnInit() {

    this.getVendorById();
    this.resetform();

  }

  vId:any;
  vendor = new Vendor();

  resetform() {
    this._service.vendor = {
      vendorId: '',
      vendorName: '',
      address: '',
      location: '',
      service: '',
      pincode: '',
      contactId: '',
      contactName: '',
      department: '',
      email: '',
      phone: '',
      isActive: ''
    }
  }
  logout(){
    this.authService.logout();
  }

  getVendorById(){
    this.resetform();
    this.vId=this.route.snapshot.paramMap.get('id');
    this._service.getVendorbyId(this.vId)
    .subscribe((data) =>{
      this.vendor=data;
      console.log('inside');
      console.log(data);
      
      console.log(this.vendor.vendorName);
    },(error)=>{
      console.log(error);
    })
  }

}
